import React from "react";
import PropTypes from "prop-types";
//import "./profile.css"

const Profile = ({id,poster,name, mail, github}) => {
    return (
      <div className="profile">
      <div className="profile_data" id = {id}>
        <div className="profile_image">
        <img className="image" src = {"images/portfolio/" + poster} alt ={name}></img>
        </div>
        <p className="profile_name">
          <span>이름</span>  {name}
        </p>
        <p className="profile_mail">
          <span>메일</span> {mail}
        </p>
      <p className="profile_github">
        <span>Github</span> {github}
      </p>
      </div>
    </div>
    )
  };

  Profile.prototypes = {
    id : PropTypes.string.isRequired,
    poster: PropTypes.string.isRequired,
    name : PropTypes.string.isRequired,
    mail : PropTypes.string.isRequired,
    github : PropTypes.string.isRequired
  }
  export default Profile;